.navigation{
  margin: 0;
  padding: 0;
  list-style: none;

  display: flex;
  flex-wrap: wrap;
  align-items: center;

  min-height: 38px;

  @media (max-width: $vp-1024) {
    min-height: 20px;
  }

  @media (max-width: $vp-767) {
    flex-direction: column;
    align-items: flex-start;
  }
}

.navigation__item {
  margin-right: 33px;

  @media (max-width: $vp-1023) {
    margin-right: 23px;
  }

  @media (max-width: $vp-767) {
    margin-bottom: 23px;
  }
}

.navigation__item:last-child {
  margin-right: 0;
}

.navigation__item a {
  display: block;
  padding: 10px 0;
  font-weight: 500;
  color: $color-alice-blue;

  @media (max-width: $vp-1023) {
    font-size: 14px;
    line-height: 17px;
  }

  @media (max-width: $vp-767) {
    font-size: 12px;
    line-height: 14px;
  }
}

.navigation__item a:hover {
  outline: 1px solid $color-alice-blue;
}
